.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

.App-link {
  color: #09d3ac;
}
.button {
  width: 100px;
  padding: 5px;
  background-color: pink;
  margin: 5px;
}
nav {
  display: flex;
  width: 100%;
  justify-content: space-around;
  background-color: darkslategray;
  padding: 10px;
}
nav a {
  color: white;
  font-size: 20px;
  text-decoration: none;
}
.entire-app{
  height: 100%;
  min-height: 100vh
}
.icons{
  color: yellow;
  font-size:  100px
}
